import React from 'react';
import './App.css'
import CheckBox from './CheckBox'
import 'bootstrap/dist/css/bootstrap.css';

export default class ToDoList extends React.Component {
    constructor(props) {
        super(props);
        this.state = {
            list: [],
            text: '',
            total: 0,
            // ids: []
        };
        this.addItem = this.addItem.bind(this);
        this.updateInput = this.updateInput.bind(this);
        this.deleteItem = this.deleteItem.bind(this)
        this.clearAll = this.clearAll.bind(this)
        // this.handleCheck = this.handleCheck.bind(this)
    };



    // getInitialState() {
    //     return { checked: true }
    // };









    // async handleCheck() {
    //     await this.setState({ checked: !this.state.checked });
    //     console.log(this.state.checked)
    // };






    updateInput(key, value) {
        this.setState({
            checked: false,
            [key]: value
        })
    };








    addItem() {
        //create item 
        if (this.state.text.slice() !== '') {
            const text = {
                id: 1 + Math.random(),
                value: this.state.text.slice()
            };
            //copy of current list items  && ids
            const list = [...this.state.list]
            // const ids = [...this.state.ids]

            //add new item to the list
            list.push(text);

            this.setState({
                list,
                text: "",
                total: this.state.list.length + 1,
                // ids:  ids.push([text.id])
            })
        }
    }










    deleteItem(id) {

        //current copy of the list of items
        const list = [...this.state.list]

        // filter out the item by item id 
        const updatedList = list.filter(item => item.id !== id);

        this.setState({
            list: updatedList,
            total: this.state.list.length - 1

        })
    };




    clearAll(){
        const clearedList = [];

        this.setState({
            list: clearedList,
            total: 0

        })
    };








    render() {
        return (
            <div className='todoListContainer'>
                <div className="container-fluid d-flex justify-content-center mt-5 toDoList">
                    <div className="row border">
                        <button type='button' className='col-2 btn btn-danger' onClick={this.clearAll}>Clear all</button>
                        <h1 className="text-center col-6">To-do List:</h1>
                        <h2 className="col-4">{this.state.total} total</h2>
                        <input
                            className='offset-2 col-4'
                            // "offset-2 col-4"
                            type="text"
                            value={this.state.text}
                            placeholder="task"
                            onChange={e => this.updateInput("text", e.target.value)}
                        />
                        <button className="col-4" onClick={this.addItem}>Add Item</button>
                        {/* list of todos */}
                        <div className='col-12'>
                            <ul className='p-2 list-unstyled'>
                                {this.state.list.map(item => {
                                    return (
                                        <li
                                            className='text-center py-2 d-flex justify-content-around'
                                            key={item.id}
                                            id={item.id}
                                        >
                                            <CheckBox/>
                                            {/* <input 
                                                className='col-1'
                                                type="checkbox"
                                                onChange={this.handleCheck}
                                                defaultChecked={this.state.checked}
                                            /> */}

                                            <p className='offset-3 col-4'>
                                                {item.value}
                                            </p>
                                            <button
                                                onClick={() => this.deleteItem(item.id)}
                                                className="offset-3 col-1 btn btn-danger"
                                            >
                                                X
                                            </button>
                                        </li>
                                    )
                                })}
                            </ul>
                        </div>
                    </div>
                </div>
            </div>
        )
    }
}
// {`${this.state.checked === true ? 'del':''} offset-2 col-4`}